<template>
  <v-menu
    :close-on-content-click="false"
    bottom
    left
    min-width="300"
    max-width="300"
    nudge-left="12"
    offset-x
    transition="slide-y-transition"
  >
    <v-btn slot="activator" class="elevation-0" color="grey" dark fab fixed style="top: 96px;" top>
      <v-icon dark color="white">list</v-icon>
    </v-btn>
    <v-card style="border-radius: 5px;">
      <div class="submenuimg">
        <p class="title"></p>
      </div>

        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0">map</h3>
          </div>
        </v-card-title>

        <v-list>
          <template v-for="(item, index) in items">
            <v-divider
              v-if="item.divider"
              :key="index"
            ></v-divider>
            <v-list-tile
              v-else
              :key="item.title"
              avatar
              @click="$vuetify.goTo(item.target, options)"
            >
              <v-list-tile-content>
                <v-list-tile-title v-html="item.title"></v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </template>
        </v-list>
      </v-card>
    </v-card>
  </v-menu>
</template>

<script>
export default {
  props: ['items'],
  data: () => ({
    options: {
      duration: 300,
      offset: 0,
      easing: 'easeInOutCubic'
    },
  })
};
</script>

<style lang="scss">
.v-avatar,
.v-responsive {
  cursor: pointer;
}
.submenuimg{
  background-image: url('../assets/submenu.jpeg');
  height: 120px;
  .title{
    text-align: center;
    line-height: 120px !important;
    font-size: 25px !important;
    color: white;
  }
}
</style>
